import { Controller, Commands } from '@ali/4ever-cangjie';
import RefBlock, { RefBlockData } from '../../mo/models';

/**
 * 更新引用块数据
 * @param controller
 * @param docKey
 * @returns
 */
export function updateRefBlockData(
  controller: Controller,
  node: RefBlock,
  data: Partial<RefBlockData>,
) {
  return controller.command(Commands.setNodeByKey, node.key, {
    data: {
      ...node.data,
      ...data,
    },
  });
}
